    display: flex;
    position: relative;
    box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
  }
}

.header-switch input {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: var(--gray-radiobtn-w);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-slider:before {
  content: "";
  height: 17px;
  width: 17px;
  background-color: var(--background-w);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.header-switch input:checked + .header-slider {
  background: linear-gradient(#9bb537, #9bb537);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-switch input:checked + .header-slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.mobile-menu {
  background-color: var(--green);
