.backdrop {
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: var(--gray-title-w);
  transition: opacity var(--trns-modal), visibility var(--trns-modal);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .15s;
}

.modal {
  background: var(--white-w);
  transition: all var(--trns-modal);
  border: 1px solid var(--gray-border-w);
  width: 335px;
  height: 582px;
  border-radius: 15px;
  margin: auto;
  padding: 28px 20px;
  transition-delay: .15s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 440px;
    height: 632px;
    padding: 40px;
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transition-delay: 0s;
  transform: scale(.75)translate(-65%, -65%);
}

.close {
  cursor: pointer;
  float: right;
  cursor: pointer;
  transition: transform var(--trns-modal);
  font-size: 28px;
  font-weight: bold;
  position: fixed;
  top: 14px;
  right: 14px;
}

.close:hover, .close:focus {
  transform-origin: center;
  transform: scale(1.8)rotate(180deg);
}

@media screen and (min-width: 768px) {
